@media (min-width: 1200px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl {
    max-width: 1200px;
  }
}
.page #banner {
  position: relative;
}
.page #banner .texts {
  position: absolute;
  bottom: 20%;
  font-weight: bold;
}
.page #banner .texts h1 {
  font-size: 30px;
  line-height: 37px;
  color: #fff;
  margin-bottom: 3px;
  font-weight: bold;
}
.page #banner .texts h1 span {
  color: #cae098;
}
.page #banner .texts p {
  font-size: 20px;
  line-height: 24px;
  color: #fff;
}
.page#page-sobre-nos .container-sobre {
  position: relative;
  background: #006635;
  z-index: 0;
}
.page#page-sobre-nos .container-sobre.show-before::before {
  content: "";
  position: absolute;
  top: 2%;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("../../assets/images/before_sobre_sec1.png") no-repeat left bottom;
  background-size: contain;
  opacity: 0.2;
  z-index: -1;
}
.page#page-sobre-nos .container-sobre.show-after::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50%;
  background: url("../../assets/images/before-section.png") no-repeat left bottom;
  background-size: cover;
  opacity: 0.07;
  filter: invert(1);
  -webkit-mask-image: linear-gradient(to top, rgb(0, 0, 0), rgba(0, 0, 0, 0));
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-size: cover;
  mask-image: linear-gradient(to top, rgb(0, 0, 0), rgba(0, 0, 0, 0));
  mask-repeat: no-repeat;
  mask-size: cover;
  z-index: -2;
}
.page#page-sobre-nos .container-sobre.show-after::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 20%;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.5), transparent);
  z-index: -1;
}
.page#page-sobre-nos .container-sobre .compromisso {
  background-color: rgba(0, 0, 0, 0.22);
  border-radius: 5px;
  padding: 45px;
}
.page#page-sobre-nos .container-sobre .compromisso p {
  margin-bottom: 0;
}
.page#page-sobre-nos #video {
  background: #d9d9d9;
  padding-bottom: 80px;
}
.page#page-nossa-historia .container-sobre {
  position: relative;
  background: #006635;
  z-index: 0;
}
.page#page-nossa-historia .container-sobre::before {
  content: "";
  position: absolute;
  top: 2%;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("../../assets/images/before_sobre_sec1.png") no-repeat left bottom;
  background-size: contain;
  opacity: 0.2;
  z-index: -1;
}